.img{
    width: 300px;
    height: auto
}
.page_block{
    padding: 15px;
    background-color: #ffffff;
    margin: 15px 0px 0px 10px;
}
.contacts{
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}
.page_info_wrap{
    text-align: center;
    width: 540px;
    margin: 15px 20px 0px 0px;
    background-color: #fff;
    box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
    padding: 15px 20px;
    border-radius: 4px;
}
.page_top{
    border-bottom: 1px solid #e7e8ec;
}
.page_info{
    padding-top: 10px;
}
.page_post{
    box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
    border-radius: 4px;
    margin: 10px 15px 0px 0px;
    padding: 15px 20px;
    background-color: #ffffff;
}
.imgFile{
    margin-top: 10px;
}
.contacts{
    margin-top: 5px;
}
.page_job{
    position: relative;
    left: 10px;
}
.block{
    margin-bottom: 10px;
}
.btn{
    margin-bottom: 30px;
}